@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

*,
*::before,
*::after {
	margin: 0;
	padding: 0;
}

:root {
	--key-color-present: #818384;
	--key-color-absent: #3a3a3c;
	--key-color-yellow: #b59f3b;
	--key-color-green: #538d4e;
}

/* Shoutout to: https://stackoverflow.com/questions/826782/how-to-disable-text-selection-highlighting */
.noselect {
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */
	-khtml-user-select: none; /* Konqueror HTML */
	-moz-user-select: none; /* Old versions of Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently
									supported by Chrome, Edge, Opera and Firefox */
}

@keyframes spin1 { 100% { transform:rotate(360deg); } }

/* Button */

.letter {
	border: none;
	height: 58px;
	cursor: pointer;
	border-radius: 4px;
	flex: 1;
	background-color: var(--key-color-present);
	margin-right: 6px;
	display: flex;
	justify-content: center;
	align-items: center;
	width: max-content;
	color: white;
	font-weight: bold;
}

.backspace {
	font-size: 1.75em;
	padding: 0.25em;
}

/* Keyboard */

.keyboard-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: black;
	padding: 1em;
	gap: 0.5em;
}

.keyboard-line {
	display: flex;
	width: 100%;
	margin: 0 auto 8px;
}

.enter {
	padding: 0.25em;
}

/* Navbar */

.navbar {
	background-color: black;
	color: white;
	display: flex;
	gap: .5em;
	align-items: center;
	padding: 1em;
	border-bottom: 1px solid #3a3a3c;
	font-family: "Roboto", sans-serif;
}

.icons {
	display: flex;
	gap: .25em;
	font-size: 1.5em;
}

.page-title {
	margin-right: auto;
}

/* Gameboard */

.gameboard {
	background-color: black;
	padding: 20px;
	display: flex;
	flex-direction: column;
	gap: 5px;
	justify-content: center;
	flex: 1;
}

.board-row {
	display: flex;
	gap: 5px;
	align-items: center;
	justify-content: center;
}

.board-square {
	background-color: var(--key-color-absent);
	padding: 1rem;
	color: white;
	font-family: "Roboto", sans-serif;
	line-height: 1em;
	height: 1em;
	width: 1em;
	font-size: 2em;
	display: flex;
	justify-content: center;
	align-items: center;
	animation-name: spin1;
}

.valid-letter {
	background-color: var(--key-color-yellow);
}

.correct-letter-space {
	background-color: var(--key-color-green);
}

.invalid-letter {
	background-color: var(--key-color-absent);
}

.column0 {
	animation-delay: 100ms;
}

.column1 {
	animation-delay: 200ms;
}

.column2 {
	animation-delay: 300ms;
}

.column3 {
	animation-delay: 400ms;
}

.column4 {
	animation-delay: 500ms;
}

@media (min-width: 550px) {
	.letter {
		padding: 1em;
	}

	.keyboard-line {
		justify-content: center;
	}
}

@media (min-width: 700px) {
	.navbar {
		justify-content: space-between;
	}
	
	.page-title {
		margin-right: 0;
	}

	.letter {
		font-size: 1em;
	}

	.backspace {
		font-size: 1.75em;
	}
}
